import React from 'react';
import NewsCard from '../NewsCard/NewsCard';
import {Grid, Grow,Typography} from '@material-ui/core';
// import classes from '*.module.scss';
import useStyles from './styles';
import './NewsCards.css';

const infoCards = [
    { color: '#00838f', title: 'Latest News', text: 'Give me the latest news' },
    {  color: '#1565c0', title: 'News by Categories', info: 'Business, Entertainment, General, Health, Science, Sports, Technology', text: 'Give me the latest Technology news' },
    {  color: '#4527a0', title: 'News by Terms', info: 'Bitcoin, PlayStation 5, Smartphones, Donald Trump...', text: 'What\'s up with PlayStation 5' },
    {  color: '#283593', title: 'News by Sources', info: 'CNN, Wired, BBC News, Time, IGN, Buzzfeed, ABC News...', text: 'Give me the news from CNN' },
  ];

const NewsCards = ({articles,activeArcticel}) =>{
    const classes = useStyles;
    if(!articles.length){
        return (
            <Grow in>
              <Grid className={classes.container} container alignItems="stretch" spacing={3}>
                {infoCards.map((infoCard) => (
                  <Grid item xs={12} sm={6} md={4} lg={3} className={classes.infoCard}>
                    <div className={classes.card} style={{ borderRadius:'3vh', backgroundColor: infoCard.color }}>
                      <Typography variant="h5" style={{marginLeft:'1.3vh'}} component="h5">{infoCard.title}</Typography>
                      {infoCard.info ? <Typography style={{marginLeft:'1.3vh'}} variant="h6" component="h6"><strong>{infoCard.title.split(' ')[2]}</strong>: <br />{infoCard.info}</Typography> : null}
                      <Typography style={{marginLeft:'1.3vh'}} variant="h6" component="h6">Try saying: <br /> <i>{infoCard.text}</i></Typography>
                    </div>
                  </Grid>
                ))}
              </Grid>
            </Grow>
          );
    }
    return (
        <div className="main-div">
        <Grow className="Grow-1"   in>
        <Grid className={classes.container} constiner alignItems="stretch" spacing={3}>

          {articles.map( (article, i)=>{
          return  <Grid key={i} item xs={12} sm={6} md={4} lg={3} style={{display: 'flex'}}>
             <NewsCard style={{ display: 'flex'}} article={article} activeArcticel={activeArcticel} i={i}/>
            </Grid>
          })}

          </Grid>
        </Grow>
        </div>
    );
}

export default NewsCards;